import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function graficos_data(inicio, fin, cod_cliente, codempresa) {
    return axios.post(`${baseURL}dashboard/graficos_hoy`,
        {
            inicio: inicio,
            fin: fin,
            codcliente: cod_cliente,
            codempresa: codempresa
        })
}

export function data_mapa(cod_cliente, fecha, codempresa) {
    return axios.post(`${baseURL}dashboard/graficos_mes`,
        {
            codcliente: cod_cliente,
            fecha: fecha,
            codempresa: codempresa
        })
}