<template>
  <section class="section-dashboard min-h-full w-full pb-[5rem] lg:pb-[2rem]">
    <h3 class="title-vista text-2xl font-semibold text-neutral-600">Dashboard</h3>
    <div class="w-full mt-3 p-4 rounded-xl bg-white">
      <div>
        <h4 class="pb-4 text-sm">Filtros avanzados</h4>
      </div>
      <div class="lg:flex lg:gap-3 lg:justify-between md:block">
        <div class="lg:flex gap-3 md:grid md:grid-cols-4 grid grid-cols-2 pb-3 lg:pb-0">
          <button
            :class="['px-5 bg-blue-500 text-white rounded-lg text-sm hover:bg-blue-600 transition duration-300 min-h-[2.5rem] lg:min-h-[1rem]', { 'bg-blue-800': selectedButton === 'viewDataDay' }]"
            @click="viewDataDay">
            Hoy
          </button>
          <button
            :class="['px-5 bg-blue-500 text-white rounded-lg text-sm hover:bg-blue-600 transition duration-300 min-h-[2.5rem] lg:min-h-[1rem]', { 'bg-blue-800': selectedButton === 'viewDataWeek' }]"
            @click="viewDataWeek">
            Semana
          </button>
          <button
            :class="['px-5 bg-blue-500 text-white rounded-lg text-sm hover:bg-blue-600 transition duration-300 min-h-[2.5rem] lg:min-h-[1rem]', { 'bg-blue-800': selectedButton === 'viewDataMes' }]"
            @click="viewDataMes">
            Mes
          </button>
          <button
            :class="['px-5 bg-blue-500 text-white rounded-lg text-sm hover:bg-blue-600 transition duration-300 min-h-[2.5rem] lg:min-h-[1rem]', { 'bg-blue-800': selectedButton === 'viewDataYear' }]"
            @click="viewDataYear">
            Año
          </button>
        </div>
        <div date-rangepicker class="flex items-center lg:pl-5 pl-0">
          <input v-model="start_date" name="start_date" type="date"
            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:outline-none focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
            placeholder="Select date start">
          <span class="mx-4 text-gray-500">hasta</span>
          <input v-model="end_date" name="end_date" type="date"
            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:outline-none focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
            placeholder="Select date end">
          <button
            class="bg-orange-400 rounded-lg px-5 text-white h-full my-2 ml-3 flex items-center min-h-[2.5rem] lg:min-h-[1rem] hover:bg-orange-600 transition duration-300" @click="viewDataInputDate">
            <span>Buscar</span>
          </button>
        </div>
      </div>
    </div>
    <div class="container-charts pt-5">
      <DashboardResumen v-if="dataTablaResumenReturn" :resumen="dataTablaResumenReturn" />
      <div class="charts doughnutChart">
        <h4 class="pb-3 font-semibold">GLNS Abastecidos</h4>
        <DoughnutChart :chartData="chartAbastecidosEmpresa" />
      </div>
      <div class="charts">
        <h4 class="pb-3 font-semibold">GLNS abastecidos por cisterna</h4>
        <BarChart :chartData="chartAbastecidosCisterna" />
      </div>
    </div>
    <div class="container-charts-2 w-full flex flex-row gap-3 pb-5">
      <div class="w-full bg-white basis-1/2 p-3 rounded-xl mt-3">
        <h4 class="pb-3 font-semibold text-center pt-2">GLNS abastecidos por día en el mes</h4>
        <BarChart :chartData="acumuladoDias" />
      </div>
      <div class="w-full bg-white basis-1/2 p-3 rounded-xl mt-3">
        <h4 class="pb-3 font-semibold text-center pt-2">GLNS abastecidos por mes en el año</h4>
        <LineChart :chartData="chartLineMeses" />
      </div>
    </div>
    <div class="w-full pb-10">
      <div class="w-full bg-white p-3 rounded-xl pb-5">
        <h4 class="font-semibold text-center mb-2">Abastecimientos concurrentes</h4>
        <HeatMap v-if="dataMapa.length > 0" :datalist="dataMapa" />
      </div>
    </div>
  </section>
</template>

<script>
import { defineComponent, onMounted, ref, computed } from 'vue';
import { DoughnutChart, BarChart, LineChart } from 'vue-chart-3';
import { Chart, registerables } from "chart.js";
import { graficos_data, data_mapa } from '@/services/DashboardService';
import DashboardResumen from '@/components/dashboard/DashboardResumen.vue';
import HeatMap from '@/components/dashboard/HeatMap.vue';
import store from '@/store'

Chart.register(...registerables);

const obtenerLabelsDias = () => {
  const labelsDias = [];
  const currentDate = new Date();
  const numberOfDays = currentDate.getDate();
  for (let i = 1; i <= numberOfDays; i++) {
    const date = new Date(currentDate.getFullYear(), currentDate.getMonth(), i);
    const formattedDate = `${date.getDate()} ${date.toLocaleString('default', { month: 'long' })}`;
    labelsDias.push(formattedDate);
  }
  return labelsDias;
};

export default defineComponent({
  name: "DashboardView",
  components: { DoughnutChart, BarChart, DashboardResumen, LineChart, HeatMap },
  setup() {
    const gnlsAbastecido = ref([]);
    const dataMapa = ref([]);
    const gnlsCisternaGalones = ref([]);
    const galonesMes = ref([]);
    const galonesDias = ref([]);
    const start_date = ref(null);
    const end_date = ref(null);
    const dataTablaResumenReturn = ref(null);
    const labelsDias = obtenerLabelsDias();
    const selectedButton = ref('');

    const formarEstructuraFecha = (currentday) => {
      const year = currentday.getFullYear();
      const month = String(currentday.getMonth() + 1).padStart(2, '0');
      const day = String(currentday.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    }

    const dataTablaResumen = (data) => {
      const dataResumen = {}
      dataResumen["clientesAbastecidos"] = data.clientegalones.filter(item => item.suma != 0).length;
      dataResumen["cisternasOperativas"] = data.cisternasoperativas.length;
      dataResumen["galonesTotales"] = data.galonestotales[0].suma;
      dataResumen["unidadesAbastecidas"] = data.equipounidades.length;
      dataResumen["ticketsEmitidos"] = data.numerotickets.length;
      return dataResumen
    }

    const viewDataInputDate = () => {
      // Obtener los valores de las fechas
      selectedButton.value = "";
      const startDate = start_date.value;
      const endDate = end_date.value;
      methodSearchData(startDate, endDate);
    }

    const methodSearchData= (Dateinicio,DateFin )=> {
      graficos_data(Dateinicio, DateFin, store.state.codcliente, store.state.codempresa)
        .then(response => {
          gnlsAbastecido.value = response.data.data.clientegalones;
          gnlsCisternaGalones.value = response.data.data.cisternagalones;
          dataTablaResumenReturn.value = dataTablaResumen(response.data.data);
        });
      data_mapa(store.state.codcliente, Dateinicio, store.state.codempresa)
        .then(response => {
          dataMapa.value = response.data.data.dataMapacalor;
          galonesMes.value = response.data.data.galones_de_todos_los_meses;
          galonesDias.value = response.data.data.galones_de_todos_los_dias;
        });
    }

    const viewDataMes = () => {
      selectedButton.value = 'viewDataMes';
      const currentDate = new Date();
      // Obtengo la fecha del inicio del mes en formato 2023-05-01
      const firstDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);
      const Dateinicio = formarEstructuraFecha(firstDayOfMonth);
      // Obtengo la fecha del dia siguiente en formato 2023-05-25
      const nextDay = new Date();
      nextDay.setDate(currentDate.getDate() + 1);
      const DateFin = formarEstructuraFecha(nextDay);
      methodSearchData(Dateinicio, DateFin);
    }

    const viewDataWeek = () => {
      selectedButton.value = 'viewDataWeek';
      const currentDate = new Date();

      // Obtengo el día de la semana (0: Domingo, 1: Lunes, ..., 6: Sábado)
      const dayOfWeek = currentDate.getDay();

      // Obtengo la fecha del inicio de la semana restando los días correspondientes al día de la semana actual
      const firstDayOfWeek = new Date(currentDate);
      firstDayOfWeek.setDate(currentDate.getDate() - dayOfWeek);
      const Dateinicio = formarEstructuraFecha(firstDayOfWeek);

      // Obtengo la fecha del día siguiente en formato 2023-05-02
      const nextDay = new Date();
      nextDay.setDate(currentDate.getDate() + 1);
      const DateFin = formarEstructuraFecha(nextDay);
      methodSearchData(Dateinicio, DateFin);
    }

    const viewDataDay = () => {
      selectedButton.value = 'viewDataDay';
      // Obtengo la fecha actual en formato 2023-05-25
      const currentDate = new Date();
      const Dateinicio = formarEstructuraFecha(currentDate);
      // Obtengo la fecha del dia siguiente en formato 2023-05-25
      const nextDay = new Date();
      nextDay.setDate(currentDate.getDate() + 1);
      const DateFin = formarEstructuraFecha(nextDay);
      methodSearchData(Dateinicio, DateFin);
    }

    const viewDataYear = () => {
      selectedButton.value = 'viewDataYear';
      const currentDate = new Date();
      // Obtengo el año actual
      const year = currentDate.getFullYear();
      // Obtengo la fecha del inicio del año en formato 2023-01-01
      const firstDayOfYear = new Date(year, 0, 1);
      const Dateinicio = formarEstructuraFecha(firstDayOfYear);
      // Obtengo la fecha del día siguiente en formato 2023-05-02
      const nextDay = new Date();
      nextDay.setDate(currentDate.getDate() + 1);
      const DateFin = formarEstructuraFecha(nextDay);
      methodSearchData(Dateinicio, DateFin);
    }

    onMounted(() => {
      // Obtengo la fecha actual en formato 2023-05-25
      const currentDate = new Date();
      const Dateinicio = formarEstructuraFecha(currentDate);
      // Obtengo la fecha del dia siguiente en formato 2023-05-25
      const nextDay = new Date();
      nextDay.setDate(currentDate.getDate() + 1);
      const DateFin = formarEstructuraFecha(nextDay);
      methodSearchData(Dateinicio, DateFin);
    });

    const chartAbastecidosEmpresa = computed(() => ({
      labels: gnlsAbastecido.value.map(item => item.cliente),
      datasets: [
        {
          data: gnlsAbastecido.value.map(item => item.suma),
          backgroundColor: ["#EC7063", "#5DADE2", "#76D7C4", "#FAD7A0", "rgba(250, 163, 64, 0.815)", "#0AF2F9", "#A66E07", "#A61F7A", "#16F27D", "#458A8C", "#0A733B", "#F2BB16"],
        },
      ],
    }));

    const chartAbastecidosCisterna = computed(() => ({
      labels: gnlsCisternaGalones.value.map(item => item.cisterna),
      datasets: [
        {
          backgroundColor: ["#EC7063", "#5DADE2", "#76D7C4", "#FAD7A0", "rgba(250, 163, 64, 0.815)"],
          data: gnlsCisternaGalones.value.map(item => item.cantidad),
          label: ["Cisternas"]
        }
      ]
    }));

    const acumuladoDias = computed(() => ({
      labels: labelsDias,
      datasets: [
        {
          backgroundColor: ["#EC7063"],
          data: galonesDias.value.map(item => item[0].suma),
          label: ["Galones abastecidos por día"]
        }
      ]
    }))

    const chartLineMeses = computed(() => ({
      labels: ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Setiembre", "Octubre", "Noviembre", "Diciembre"],
      datasets: [
        {
          data: galonesMes.value.map(item => item[0].suma),
          backgroundColor: ["#5DADE2"],
          label: ["Galones Abastecidos por mes"]
        },
      ],
    }));

    return {
      chartAbastecidosEmpresa,
      chartAbastecidosCisterna,
      start_date,
      end_date,
      viewDataInputDate,
      viewDataMes,
      viewDataDay,
      viewDataWeek,
      viewDataYear,
      selectedButton,
      dataTablaResumenReturn,
      dataMapa,
      galonesDias,
      galonesMes,
      acumuladoDias,
      chartLineMeses
    };
  },
});

</script>

<style lang="scss" scoped>
.container-charts {
  display: flex;
  width: 100%;
  gap: 15px;

  .charts {
    text-align: center;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.815);
    padding: 25px;
    border-radius: 5%;
  }
}

@media screen and (min-width: 660px) and (max-width: 1200px) {
  .container-charts {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 660px) {
  .section-dashboard {
    h4 {
      font-size: 13px;
    }

    .container-charts-2 {
      display: block;
    }

    .container-charts {
      display: block;

      .charts {
        margin-bottom: 10px;
      }
    }
  }

}
</style>